<script setup lang="ts">
import useAsync from "~/composables/use-async";
import CategoriesAPI from "~/services/categories";

const { t } = useI18n();
const route = useRoute();
const identityId = route.params.mainId as string;

const onError = () => showError({ statusCode: 404 });
const { data: category, loading: loadingCategory, run: loadCategory } = useAsync(CategoriesAPI.loadById, { onError });

const breadcrumbs = computed(() => [
  { to: "/following", title: t("labels.following") },
  { to: `/following/category/${identityId}/followed`, title: loadingCategory ? t("labels.loading") : category?.value?.title ?? "", active: true },
]);

onMounted(() => {
  loadCategory(identityId);
});

useHead({ title: computed(() => category.value?.title) as MaybeRef<string> });
definePageMeta({ layout: "home", middleware: "auth" });
</script>

<template>
  <div class="main-container">
    <BaseBreadcrumbs class="mb-10" :items="breadcrumbs" />

    <ListIdentityItems
      has-load-more-button
      :identity-id="identityId"
      load-only-followed
      link-list
    >
      <template #start>
        <div class="mt-10 mb-6 text-2xl font-bold text-main">
          {{ loadingCategory ? t("labels.loading") : category?.title }}
        </div>
      </template>
    </ListIdentityItems>
  </div>
</template>
